import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import { State, Getter, Action, Mutation } from "vuex-class";
import Url from "../../../api";

@Component({
  components: {}
})
export default class AdminData extends Vue {
  @Prop({ type: String, default: "" })
  id?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  addDialog = false;
  tableData: Array<any> = [];

  @Watch("dialogVisible")
  handleInfo(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.handleShowAdmin();
    }
  }

  async handleShowAdmin() {
    let {
      data: { data, status, message }
    } = await this.$http.get(Url.getAdminByTenant + this.id);
    if (status) {
      this.tableData = data;
    } else {
      this.$message.error(message);
    }
  }

  closeDialog() {
    this.$emit("close");
  }
}
