









































































import { Vue, Component, Prop } from "vue-property-decorator";
import TeamManagementTeam from "./TeamManagementTeam.vue";
import AdminData from "./AdminData.vue";

import Url from "../../../api";

@Component({
  components: {
    TeamManagementTeam,
    AdminData
  }
})
export default class TeamManagementList extends Vue {
  // tenantTypeMap: any = {
  //   SYSTEM: "系统团队",
  //   INTENANT: "用户团队"
  // };
  loading: boolean = false;
  tableData: Array<any> = [];
  name: string = "";
  // tenantType: string = "";
  pagination: any = {
    current: 1,
    size: 10
  };
  total: number = 0;

  teamVisible: boolean = false;
  teamItem: any = {};

  currentId = "";
  adminDialog = false;

  created(): void {
    this.getTableData();
  }

  search() {
    this.pagination.current = 1;
    this.getTableData();
  }

  async getTableData() {
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.getTenantList, {
        ...this.pagination,
        query: {
          name: this.name
        }
      });
      if (status) {
        this.tableData = data.records;
        this.total = data.total;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleCurrentChange(val: number) {
    this.pagination.current = val;
    this.getTableData();
  }

  handleSizeChange(val: number) {
    this.pagination.size = val;
    this.getTableData();
  }

  // 新增团队
  handleAdd() {
    this.teamItem = {};
    this.teamVisible = true;
  }

  handleHiddenTeam(flag: boolean) {
    this.teamVisible = false;
    if (flag) {
      this.search();
    }
  }

  // 修改团队
  async handleEditRole(item: any) {
    this.teamItem = item;
    this.teamVisible = true;
  }
  //删除团队
  handleDelete(item: any) {
    let _this = this;
    this.$confirm("确认删除？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(Url.deleteTenant + "/" + item.id).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              //重新获取列表
              _this.getTableData();
            } else {
              _this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }

  // 管理员
  handleAdmin(row: any) {
    this.currentId = row.id;
    this.adminDialog = true;
  }

  // 菜单
  handleEditNav(item: any) {
    this.$emit("showNav", item);
  }

  // App
  handleEditApp(item: any) {
    this.$emit("showApp", item);
  }

  // baseType
  handleEditBaseType(item: any) {
    this.$emit("showBaseType", item);
  }

  closeAdminDialog() {
    this.adminDialog = false;
  }
}
