 * @Description: 团队管理菜单表单
 * @Author: liuyang
 * @Date: 2019-06-28 11:39
 */
import { Vue, Component, Prop } from "vue-property-decorator";

import Url from "../../../api";

@Component({})
export default class TeamManagementNav extends Vue {
  @Prop({ type: Object, required: true })
  data!: any;
  loading: boolean = false;
  allNavTree: Array<any> = [];
  teamNavIdList: Array<string> = [];
  defaultProps = {
    children: "children",
    label: "name"
  };

  created(): void {
    this.matchOperation();
  }

  async matchOperation() {
    this.loading = true;
    try {
      await Promise.all([this.getTeamNavList(), this.getAllNavList()]);
    } finally {
      this.loading = false;
    }
  }

  async getAllNavList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(Url.getMenuList);
    if (status) {
      this.allNavTree = data;
    } else {
      this.$message.error(message);
    }
  }

  async getTeamNavList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(Url.getTenantNavList + this.data.id);
    if (status) {
      this.teamNavIdList = data.map((d: any) => d.id);
    } else {
      this.$message.error(message);
    }
  }

  async handleSave() {
    this.loading = true;
    const tree: any = this.$refs.tree;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.updateTenantNav + this.data.id, tree.getCheckedNodes().map((d: any) => d.id));
      if (status) {
        this.$message.success("保存成功");
        this.handleBack();
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleBack() {
    this.$emit("hidden");
  }
}
