 * @Description: 团队管理app表单
 * @Author: liuyang
 * @Date: 2019-07-01 10:07
 */
import { Vue, Component, Prop } from "vue-property-decorator";

import Url from "../../../api";

@Component({})
export default class TeamManagementApp extends Vue {
  @Prop({ type: Object, required: true })
  data!: any;
  loading: boolean = false;
  tableData: Array<any> = [];
  allAppIdMap: any = {};
  name: string = "";
  pagination: any = {
    current: 1,
    size: 10
  };
  total: number = 0;

  async created() {
    await this.getAllAppList();
    this.getTableData();
  }

  async getTableData() {
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.getAPPManagementList, {
        ...this.pagination,
        query: {
          name: this.name
        }
      });
      if (status) {
        this.tableData = data.records;
        this.total = data.total;
        this.$nextTick(() => {
          this.tableData
            .filter((d: any) => this.allAppIdMap[d.id])
            .forEach(row => {
              let multipleTable: any = this.$refs.multipleTable;
              multipleTable.toggleRowSelection(row, true);
            });
        });
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleCurrentChange(val: number) {
    this.pagination.current = val;
    this.getTableData();
  }

  async getAllAppList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(Url.getAllAppList + this.data.id);
    if (status) {
      data.forEach((item: any) => {
        this.allAppIdMap[item.id] = true;
      });
    } else {
      this.$message.error(message);
    }
  }

  // 获得所有复选框选中数据
  handleSelectTable(arr: Array<any>, item: any) {
    if (arr.includes(item)) {
      this.allAppIdMap[item.id] = true;
    } else {
      delete this.allAppIdMap[item.id];
    }
  }

  handleSelectAllTable(arr: Array<any>) {
    if (arr.length) {
      this.tableData.forEach((item: any) => {
        this.allAppIdMap[item.id] = true;
      });
    } else {
      this.tableData.forEach((item: any) => {
        delete this.allAppIdMap[item.id];
      });
    }
  }

  async handleSave() {
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.updateMobileApp + this.data.id, Object.keys(this.allAppIdMap));
      if (status) {
        this.$message.success("保存成功");
        this.handleBack();
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleBack() {
    this.$emit("hidden");
  }
}
