var cdnConfig = {
    AMap: "https://webapi.amap.com/maps?v=1.4.11&key=fbc043611829448a8d799c0a5fef544a&plugin=AMap.Geocoder,AMap.PlaceSearch&callback=onAMapLoad",
    AMapUI: "https://webapi.amap.com/ui/1.0/main-async.js?v=1.0.11"
};
export function loadAMap(aMapSrc) {
    return new Promise(function (resolve, reject) {
        var win = window;
        var newSrc = "https://webapi.amap.com/maps?v=1.4.11&key=" + (aMapSrc || "fbc043611829448a8d799c0a5fef544a") + "&plugin=AMap.Geocoder,AMap.PlaceSearch&callback=onAMapLoad";
        if (win.AMap && !win.oldSrc && win.oldSrc === newSrc) {
            return resolve();
        }
        var script = document.createElement("script");
        win.onAMapLoad = function () {
            if (win.initAMapUI) {
                win.initAMapUI();
            }
            resolve();
        };
        script.onerror = reject;
        script.src = newSrc;
        document.head.appendChild(script);
        win.oldSrc = newSrc;
    });
}
export function loadAMapUI() {
    return new Promise(function (resolve, reject) {
        var win = window;
        if (win.AMap) {
            return resolve();
        }
        var script = document.createElement("script");
        script.onload = function () {
            if (win.AMap) {
                win.initAMapUI();
            }
            resolve();
        };
        script.onerror = reject;
        script.src = cdnConfig.AMapUI;
        document.head.appendChild(script);
    });
}
