import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";

@Component({})
export default class TeamManagementBaseType extends Vue {
  @Prop({ type: Object, required: true })
  data!: any;

  loading: boolean = false;
  dataList: Array<any> = [];
  checkList: Array<any> = [];

  async created() {
    this.loading = true;
    try {
      await Promise.all([this.getDataList(), this.getCheckList()]);
    } finally {
      this.loading = false;
    }
  }

  async getDataList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(Url.getBaseTypeList);
    if (status) {
      this.dataList = data;
    } else {
      this.$message.error(message);
    }
  }

  async getCheckList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(Url.getTeamBaseType + this.data.id);
    if (status) {
      this.checkList = data.map((d: any) => d.id);
    } else {
      this.$message.error(message);
    }
  }

  async handleSave() {
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.saveTeamBaseType + this.data.id, this.checkList);
      if (status) {
        this.$message.success("保存成功");
        this.handleBack();
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleBack() {
    this.$emit("hidden");
  }
}
