 * @Description: 团队管理团队表单
 * @Author: liuyang
 * @Date: 2019-06-28 10:42
 */
import { Vue, Component, Prop, Watch } from "vue-property-decorator";

import Url from "../../../api";
import QtouchAMap from "@/components/qTouch/aMap/QtouchAMap.vue";
import dictList from "@/utils/dictList";
import utils from "@/utils/utils";

@Component({
  components: { QtouchAMap }
})
export default class TeamManagementTeam extends Vue {
  @Prop({ type: Boolean, default: false })
  value!: boolean;

  @Prop({
    type: Object,
    default: () => ({
      defaultRole: false
    })
  })
  data!: any;

  @Watch("value")
  newValue(val: boolean) {
    this.addDialog = val;
    if (val && this.data) {
      this.$nextTick(() => {
        const form: any = this.$refs.form;
        form.resetFields();
        this.form = JSON.parse(JSON.stringify(this.data));
      });
    }
  }

  addDialog = false;

  form: any = {};
  formRules: any = {
    name: {
      required: true,
      message: "该选项为必填项",
      trigger: "change"
    },
    code: {
      required: true,
      message: "该选项为必填项",
      trigger: "change"
    }
  };
  loading: boolean = false;
  openMapEvent: boolean = false;
  mapType: any[] = [];
  coordinateType: any[] = [];

  handleSave() {
    const form: any = this.$refs.form;
    form.validate((flag: boolean) => {
      if (flag) {
        this.saveForm();
      }
    });
  }

  async saveForm() {
    this.loading = true;
    try {
      let url = this.form.id ? Url.updateTenant : Url.addTenant;
      let {
        data: { data, status, message }
      } = await this.$http.post(url, this.form);
      if (status) {
        this.$message.success(this.form.id ? "修改成功" : "添加成功");
        this.handleBack(true);
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleBack(flag: boolean) {
    this.$emit("hidden", flag);
  }

  changeMapCenter(lnglat: any, e: any) {
    // this.form.mapCenter = lnglat.lng + ", " + lnglat.lat;
    this.$set(this.form, "mapCenter", lnglat.lng + ", " + lnglat.lat);
  }

  changeMapZoom(zoom: any) {
    this.form.mapZoom = zoom;
  }

  async getMapTypeList() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.getDictList, {
        dictTypeCode: "TENANT_MAP"
      });
      if (status) {
        this.mapType = data;
      } else {
        this.$message.error("地图类型查询失败：" + message);
      }
    } catch (e) {
      console.log(e);
    }
  }

  async getCoordinateTypeList() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.getDictList, {
        dictTypeCode: "COORDINATE_TYPE"
      });
      if (status) {
        this.coordinateType = data;
      } else {
        this.$message.error("坐标系类型查询失败：" + message);
      }
    } catch (e) {
      console.log(e);
    }
  }

  changeMapCoordinate(val: string) {
    if (val === "AMAP") {
      this.form.centerCoordinateType = "GCJ02";
    }
  }

  created() {
    this.getCoordinateTypeList();
    this.getMapTypeList();
  }
}
