 * @Description: -
 * @Author: -
 * @Date: -
 **/
import { loadAMap, loadAMapUI } from "common-config/esm/utils/loadAMap";
import { Vue, Component, Prop, Watch } from "vue-property-decorator";

@Component({})
export default class QtouchAMap extends Vue {
  // 基础地图配置信息
  @Prop({ type: [Array, String], default: () => [106.569137, 29.643814] })
  mapCenter!: any;
  @Prop({ type: String, default: "default" })
  mapStyle!: string;
  @Prop({ type: Number, default: 3 })
  mapZoom!: number;
  @Prop({ type: Object })
  mapOptions!: any;
  // 启用地图事件
  @Prop({ type: Boolean, default: true })
  mapEvent!: boolean;

  // 组件内部变量
  map: any;

  // 初始化时加载地图
  async created() {
    try {
      if (!(window as any).AMap) {
        await Promise.all([loadAMap(), loadAMapUI()]);
      }
      this.$nextTick(() => this.initMap());
    } catch (e) {
      console.log(e);
      this.$message.error("地图加载失败");
    }
  }

  beforeDestroy() {
    if (this.map) this.map.destroy();
  }

  initMap() {
    this.map = new (window as any).AMap.Map(this.$refs["QAMap"], {
      center: typeof this.mapCenter === "string" ? this.mapCenter.split(",") : this.mapCenter,
      zoom: this.mapZoom,
      mapStyle: `amap://styles/${this.mapStyle}`,
      ...this.mapOptions
    });
    this.map.on("click", (e: any) => {
      if (this.mapEvent) this.$emit("map-click", e.lnglat, e);
    });
    this.map.on("dblclick", (e: any) => {
      if (this.mapEvent) this.$emit("map-dblclick", e.lnglat, e);
    });
    this.map.on("rightclick", (e: any) => {
      if (this.mapEvent) this.$emit("map-rightclick", e.lnglat, e);
    });
    this.map.on("resize", (e: any) => {
      if (this.mapEvent) this.$emit("map-resize", e.lnglat, e);
    });
    this.map.on("dragend", (e: any) => {
      if (this.mapEvent) this.$emit("map-dragend", e.lnglat, e);
    });
    this.map.on("zoomend", (e: any) => {
      if (this.mapEvent) this.$emit("map-zoomend", this.map.getZoom());
    });
  }

  @Watch("mapStyle")
  handleChangeMapStyle(val: any) {
    if (this.map && val) this.map.setMapStyle(`amap://styles/${val}`);
  }
  @Watch("mapZoom")
  handleChangeMapZoom(val: any) {
    if (this.map && val) this.map.setZoom(val);
  }
  @Watch("mapCenter")
  handleChangeMapCenter(val: any) {
    if (this.map && val && val.length) {
      if (typeof val === "string") {
        val.split(",").length === 2 && this.map.setCenter(val.split(","));
      } else {
        this.map.setCenter(val);
      }
    }
  }
  @Watch("mapEventStatus")
  handleChangeMapCursor(val: boolean) {
    if (val) {
      this.map.setDefaultCursor("crosshair");
    } else {
      this.map.setDefaultCursor("default");
    }
  }

  mapGeocoderAddress(options: any, position: any) {
    return new Promise((resolve, reject) => {
      if ((window as any).AMap) {
        let defaultOptions: any = {
          radius: 1000,
          extensions: "all"
        };
        let geocoder = new (window as any).AMap.Geocoder(options || defaultOptions);
        geocoder.getAddress(position, function(status: any, result: any) {
          if (status === "complete" && result.info === "OK") {
            resolve(result);
          } else {
            reject(result);
          }
        });
      } else {
        reject();
      }
    });
  }
}
